.newTerminal {
  position: absolute;
  left: 20px;
  top: 20px;
  border: none;
  background-color: transparent;
  font-size: 12px;
  cursor: pointer;
  display: flex;
  flex-direction: column; 
  justify-content: center;
  align-items: center;
}

.newTerminal svg {
  padding: 6px;
  margin: 6px;
  background-color: rgb(var(--second-bg-color));
}

.newTerminal > svg > path {
  fill: rgb(var(--second-fg-color));
  color: rgb(var(--second-fg-color));
} 

.newTerminal:hover {
  border: 1px solid rgba(var(--main-border-color), 0.8);
}
