
@keyframes createBox {
    0% {
      transform: scale(0);
      background-color: rgb(var(--main-bg-color));
    }
    90% {
      transform: scale(0);
      background-color: rgb(var(--main-bg-color));
    }
    100% {
      transform: scale(1);
      background-color: rgba(var(--main-bg-color),0.8);
    }
  }
  
  @keyframes pulse {
    0% {
      transform: scale(1);
      box-shadow: 0 0 5px 5px rgba(var(--main-border-color), 0.1);
    }
    50% {
      transform: scale(1);
      box-shadow: 0 0 10px 10px rgba(var(--main-border-color), 0.2);
    }
    100% {
      transform: scale(1);
      box-shadow: 0 0 5px 5px rgba(var(--main-border-color), 0.1);
    }
  }
  