.terminal {
  border: 0;
  margin: 0;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding-right: 12px;
  padding-bottom: 12px;
}

.terminal-input {
  padding-left: 5px;
  padding-top: 5px;
  font-weight: bold;
  font-size: 11px;
  flex-grow: 1;
  color: rgb(var(--terminal-input-color));
  padding-bottom: 48px;
}

.terminal-input:focus {
  outline: none;
}

.terminal-input *::-moz-selection {
  color: rgb(var(--second-fg-color));
  background-color: rgba(var(--second-bg-color), 0.6);
}

.terminal-input *::selection {
  color: rgb(var(--second-fg-color));
  background-color: rgba(var(--second-bg-color), 0.6);
}

.command-output {
  display: inline-block;
}

.command-output * {
  color: rgb(var(--terminal-output-color));
  font-weight: bolder;
}

.command-output:empty, .command-output:empty+br {
  display: none;
}

.label {
  display: inline;
  border-radius: 25px;
  background: rgb(var(--terminal-output-color));
  padding: 5px;
  line-height: 30px;
  color: rgb(var(--main-bg-color)) !important;
  font-weight: bolder;
  white-space: nowrap;
}

@media screen and (min-width: 600px) {
  .commands {
    display: none !important;
  }

  .terminal-input {
    padding-bottom: 0px;
    font-size: 12px;
  }
}

.commands {
  position: fixed;
  bottom: 0;
  height: 48px;
  background-color: black;
  width: 100%;
  overflow: auto;
  display: flex;
}

.commands button {
  border-radius: 20px;
  background: rgb(var(--main-bg-color)) !important;
  border-color: rgb(var(--terminal-output-color));
  padding: 4px;
  margin: 10px 5px;
  color: rgb(var(--terminal-output-color));
  font-weight: bolder;
  white-space: nowrap;
  flex-grow: 0;
  display: inline;
}