.window {
    position: fixed;
}

.container {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    transform: scale(1);
    background-color: rgba(var(--window-bg-color), 0.8);
    box-shadow: 0 0 5px 5px rgba(var(--main-border-color), 0.1);
    animation: pulse 2s infinite 1.5s, createBox 1s;
    border: none;
    padding: 0;
    margin: 0;
}

.titlebar {
    background-color: rgba(var(--window-title-bg-color), 0.4);
    color: rgba(var(--window-title-color));
    font-weight: bold;
    min-height: 24px;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: move;
}

.content {
    font-size: 0.8em;
    text-align: left;
    overflow: auto;
    flex-grow: 1;
    display: flex;
}

.content .simplebar-visible::before {
    background-color: rgba(var(--window-scrollbar-color), 1);
}

.simplebar-content-wrapper {
    overflow: auto;
  }

.simplebar-content {
    height: 100%;
    display: flex;
}

@media only screen and (min-width: 600px) {
    #secret {
        font-size: 2em;
    }
}